@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.social-icon-link-github{
  font-size: 40px;
  color: white;
  padding-right: 2%;
  padding-top: 2%;
}

.social-icon-container {
  width: 100%;
  text-align: center;
}

.social-icon-link-linkedin{
  font-size: 40px;
  color: white;
  padding-left: 2%;
  padding-top: 2%;
}

.social-icon-link-github:hover{
  font-size: 45px;
}

.social-icon-link-linkedin:hover{
  font-size: 45px;
}

.splash-container {
  font-family: 'Source Sans Pro', sans-serif;
  color: white;
  height: 100vh;
}

.splash-container h1{
  text-align: center;
  font-size: 85px;
  padding-top: 12%;
}

.splash-container h2{
  text-align: center;
}

.splash-container li {
  padding-right: 2%;
  float: right; /* makes list horizontal */
  list-style-type: none;
  margin: 0 0 0 30px; /* separates 'buttons' */
  font-size: 23px;
}

.Portfolio-Container{
  width: 100%;
  background-color: white;
  font-family: 'Source Sans Pro', sans-serif;
  color: #98B6D4;
  height: 100vh;
}

.project-image{
  width: 30%;
  height: 50vh;
  float: left;
  background: #98B6D4;
}

.project-info {
  width: 100%;
  height: 50vh;
  float: left;
  margin-left: 2%;
}

.project-info p{
  font-size: 20px;
  font-weight: bold;
}

.project-info span{
  font-size: 20px;
  padding-left: 5%;
}

.project-title h2 {
  font-size: 30px;
  text-decoration: underline;
}

.feature-item{
  font-size: 20px;
}

.project-link-container {
  width: 100%;
  text-align: center;
}


.project-link-container a{
  font-size: 40px;
  color: #98B6D4;
  padding-left: 5%;
  padding-right: 5%;
}


.project-link-container a:hover{
  font-size: 45px;
}

.project-image-slideshow {
  margin: auto;
  position: relative;
  width: 90%;
  height: 90%;
  padding: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
}

.project-link-container button{
  color: #98B6D4;
  background: #fff;
  border: 1px solid #98B6D4;
  font-size: 17px;
  padding: 7px 12px;
  font-weight: normal;
  margin-left: auto;
  margin-right: auto;
  display: inline-block;
  text-decoration: none;
  min-width: 120px;
}

.project-link-container button:hover{
  color: #fff;
  background: #98B6D4;
}

body{
  background-color: #98B6D4
}

.App {
  font-family: 'Source Sans Pro', sans-serif;
  color: white;
  height: 100%;
}

.App h1{
  text-align: center;
  font-size: 85px;
  padding-top: 12%;
}

.App h2{
  text-align: center;
}

.App nav li {
  padding-right: 2%;
  float: right; /* makes list horizontal */
  list-style-type: none;
  margin: 0 0 0 30px; /* separates 'buttons' */
  font-size: 25px;
}

.App nav li:hover {
  font-size: 30px;
}

.Navigation-Wrapper{
  width: 100%;
  top: 0;
  position: fixed;
}

.App nav li a {
  text-decoration: none;
  color: white;
}

